.servicos {
  background-color: #152b40;
  color: #fafafa;
  height: 100%;
  width: 100%;
  text-align: center;
  margin: auto;
  padding: 0;
}

.servicos .serv-container {
  /* background-color: aqua; */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.serv-col {
  /* background-color: blue; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem;
  text-align: left;
}
.serv-col h1 {
  color: #bf9563;
  text-align: center;
}
.serv-col h3 {
  margin: 2rem 0 0.75rem 0;
  font-size: 1.75rem;
  text-decoration: underline;
}
.line {
  margin-bottom: 1.5rem;
}
.serv-col p {
  margin: 0.5rem 0;
  font-size: 1.5rem;
  font-weight: 400;
  text-align: justify;
}
.serv-col ul li {
  text-align: justify;
  font-size: 1.5rem;
  font-weight: 400;
}
.serv-col ul li::before {
  content: 'âœ…';
  font-size: 1.25rem;
  display: inline-block;
  width: 0.5rem;
  margin: 1rem 1.25rem 0 0;
}

.serv-container img {
  width: 75%;
  /* max-width: 480px; */
  margin: auto;
}

@media screen and (max-width: 980px) {
  .servicos .serv-container {
    grid-template-columns: 1fr;
  }
  .serv-container img {
    width: 90%;
  }
  .serv-col {
    padding: 0.75rem;
  }
}
