.cnh-hero {
  background-color: #152b40c8;
  color: #fafafa;
  background-size: 50px 10%;
  height: 90%;
  width: 100%;
  position: relative;
}

.cnh-hero::before {
  content: '';
  background: url('../../images/rua-cnh-suspensa.jpg') no-repeat center
    center/cover;
  /* Photo by Kaique Rocha: https://www.pexels.com/photo/parked-gray-car-39943/ */
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-color: #fafafa;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

/* 1920 x 1080  */
.cnh-hero .content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  padding: 4rem 2.5rem;
}

.cnh-hero .content img {
  width: 18%;
  max-width: 480px;
  padding: 0 1rem 2rem 1rem;
}

.cnh-hero .content h1 {
  /* color: #ff5f03; */
  color: #BF9563;
  text-shadow: 0 1px 2px #000;
}

.cnh-hero .content .cnh-text p:first-child {
  font-weight: 700;
  font-size: 1.5rem;
  margin-top: 3rem;
}

.cnh-hero .content .cnh-text p:nth-child(2) {
  font-weight: 500;
  font-size: 1.5rem;
  margin-top: 3rem;
  margin-bottom: 0;
}

/* ********************************* */
/* ******* CNH Enganjamento ******** */
/* ********************************* */
.engajamento {
  /* background-color: #152b40; */
  /* color: #fafafa; */
  height: 100%;
  width: 100%;
  display: grid;
  margin: auto;
  padding: 5rem 1.25rem;
  grid-template-columns: repeat(2, 1fr);
}

.engajamento img {
  /* background-color: #152b40; */
  width: 50vw;
  max-width: 560px;
  margin: auto;
  /* padding: 1rem; */
  box-shadow: 0 7px 10px #BF9563;
}
.engajamento .eng-conteudo {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.engajamento .eng-conteudo h1 {
  padding: 1rem 0 0 0;
  color: #152B40;
  text-shadow: 0 1px 2px #BF9563;
}
.engajamento .eng-conteudo h2 {
  padding: 3rem;
}
/* ************ MOBILE ************* */
@media screen and (max-width: 980px) {
  .cnh-hero {
    width: 100%;
  }
  .cnh-hero .content {
    padding: 1rem;
  }
  .cnh-hero::before {
    /* Canceles the parallax scrolling effect */
    background-attachment: scroll;
  }

  .cnh-hero .content img {
    width: 50%;
    max-width: 320px;
    padding: auto 0;
  }
  .cnh-hero .content h1 {
    margin-top: 1rem;
  }
  /*  */
  .cnh-hero .content .cnh-text p:first-child {
    margin-top: 1rem;
  }
  .engajamento {
    height: 100%;
    /* padding: 1.5rem; */
    justify-content: center;
    grid-template-columns: minmax(370px, 480px);
  }
}
