.wave-path-up {
  fill: #fafafa;
  transform-origin: center;
  transform: rotate(180deg);
}
.wave-path-down {
  fill: #fafafa;
  transform-origin: center;
  /* transform: rotate(180deg); */
}
.wave-svg {
  height: 50px;
  /* width: calc(100% + 1.3px); */
  width: 100%;
  margin-bottom: -.4rem;
}

@media screen and (max-width: 920px) {
  .wave-svg {
    height: 25px;
    /* width: calc(125% + 1.3px); */
    width: 100%;
  }
}
