/* **************************************** */
/* ***************** Dores **************** */
/* **************************************** */
.dores-cnh {
  background-color: #fafafa;
  color: #152b40;
  width: 100%;
  height: 100%;
  margin: auto;
  padding: 10vh 0;
}
.header-cnh {
  text-align: center;
  font-size: 1.5rem;
  padding: 1rem;
}
.header-cnh span {
  font-weight: 700;
  font-size: 2rem;
}

.dores-cnh .container-cnh {
  width: 90vw;
  max-width: 1280px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.dores-cnh .container-cnh .card-cnh {
  text-align: center;
  margin: 1rem;
  padding: 1rem;
  background-color: #fff;
  box-shadow: 0 7px 10px #152b40;
}
.dores-cnh .container-cnh .card-cnh:hover {
  transform: scale(1.1);
}
.card-cnh button {
  text-align: center;
  padding: 0.5rem;
  margin: auto;
  font-size: 1rem;
  font-weight: 700;
  background-color: #152b40bb;
  color: #fafafa;
}

.card-cnh button:hover {
  background-color: #152b40;
}

/* ************ MOBILE ************* */
@media screen and (max-width: 960px) {
  /* Dores */
  .dores-cnh {
    width: 100%;
    padding: 1rem 0;
  }
  .dores-cnh .container-cnh {
    /* grid-template-columns: 1fr; */
    grid-template-columns: minmax(370px, 480px);
    justify-content: center;
  }
}
