/* ********* CNH Bloqueada ************ */
/* ************************************ */
.cnh-bloqueada {
  min-height: 90vh;
  width: 95%;
  max-width: 1080px;
  margin: 3rem auto 1rem auto;
  padding: 2rem;
  box-shadow: 0 7px 10px #152b40;
}
.cnh-bloqueada h1 {
  text-align: center;
  padding: 2rem 0 0.5rem 0;
  font-size: 3rem;
}
.cnh-bloqueada p {
  padding: 0.5rem;
  text-align: justify;
}
.cnh-bloqueada h3 {
  padding: 1rem 0rem;
  text-transform: uppercase;
}
.cnh-bloqueada ul {
  margin-bottom: 1rem;
}
/*CSS Unicode*/
.cnh-bloqueada ul li::before {
  content: 'ðŸš«';
  font-size: 1.25rem;
  font-weight: bold;
  display: inline-block;
  width: 0.5rem;
  margin: 1rem 1.25rem 0 0rem;
}
.cnh-bloqueada button {
  text-align: center;
  margin: 3rem auto;
  padding: 0.75rem;
  font-weight: 700;
  font-size: 1.5rem;
  box-shadow: 0 7px 10px #152b40;
}
