.card-form {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fafafa;
}

 .lb-contact-form {
  background-color: #152b40D5;
  padding: 60px 80px 60px;
  text-align: center;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 0 7px 10px #bf9563;
}

.lb-contact-form h2 {
  color: #fafafa;
  font-weight: 500;
  text-transform: uppercase;
  font-size: 1.5em;
  letter-spacing: 0.1em;
  margin-bottom: 50px;
}
.input-box {
  position: relative;
  width: 100%;
  margin-bottom: 40px;
}

.input-box input {
  position: relative;
  width: 100%;
  padding: 10px 0;
  background: transparent;
  border: none;
  border-bottom: 2px solid #fafafa;
  outline: none;
  color: #fafafa;
  font-size: 1em;
  /* text-transform: uppercase; */
  letter-spacing: 0.15em;
}

.input-box textarea {
    position: relative;
    width: 100%;
    padding: 10px 0;
    background: transparent;
    border: none;
    border-bottom: 2px solid #fafafa;
    outline: none;
    color: #fafafa;
    font-size: 1em;
    /* letter-spacing: 0.15em; */
  }

.input-box textarea {
    border: 2px solid #fafafa;
}

.input-box label {
  position: absolute;
  left: 0;
  padding: 10px 0;
  pointer-events: none;
  text-transform: uppercase;
  transform: translateY(-35px) ;
}

/* .input-box label span {
  position: relative;
  display: inline-flex;
  letter-spacing: 0.05em;
  transition: 0.2s ease-in-out;

} */

/* .input-box input:focus ~ label span,
.input-box input:valid ~ label span {
  color: #bf9563;
  letter-spacing: 0.15em;
  transform: translateY(-25px) ;
} */

.input-box input:focus,
.input-box input:valid {
  border-bottom: 2px solid #bf9563;
}

.input-box textarea:focus,
.input-box textarea:valid {
  border: 2px solid #bf9563;
}

.input-box input[type='button'] {
  background: #bf9563;
  border: none;
  padding: 15px;
  color: #152b40;
  font-weight: 600;
  font-size: 1.5em;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  cursor: pointer;
  transition: 0.5s;
}
.input-box input[type='button']:hover {
  letter-spacing: 0.35em;
}

@media screen and (max-width: 980px) {
    .card-form {
        margin: 1.5rem .25rem .5rem ;
    }
    .lb-contact-form {
        padding: 1.25rem;
    }
}