/* *********** CNH Cassada ************* */
/* ************************************* */
 .cnh-cassada{
  min-height: 90vh;
  width: 95%;
  max-width: 1080px;
  margin: 3rem auto 1rem auto;
  padding: 2rem;
  box-shadow: 0 7px 10px #152b40;
} 
 .cnh-cassada h1 {
  text-align: center;
  padding: 2rem 0 .5rem 0;
  font-size: 3rem;
} 
 .cnh-cassada p{
  padding: .5rem;
  text-align: justify;
}
 .cnh-cassada h3 {
  padding: 1rem 0rem;
  text-transform: uppercase;
} 
 .cnh-cassada ul {
  margin-bottom: 1rem;
} 
/*CSS Unicode*/
 .cnh-cassada ul li::before {
  content: 'ðŸš«'; 
  font-size: 1.25rem;
  font-weight: bold;
  display: inline-block;
  width: .5rem;
  margin: 1rem 1.25rem 0 0rem;
} 
 .cnh-cassada button {
  text-align: center;
  margin: 3rem auto;
  padding: .75rem;
  font-weight: 700;
  font-size: 1.5rem;
  box-shadow: 0 7px 10px #152b40;
} 