/* ************************************ */
/* *********** CNH Info *************** */
/* ************************************ */
.cnh-suspensa {
  min-height: 90vh;
  width: 95%;
  max-width: 1080px;
  margin: 3rem auto 1rem auto;
  padding: 2rem;
  box-shadow: 0 7px 10px #152b40;
}
.cnh-suspensa h1 {
  text-align: center;
  padding: 2rem 0 0.5rem 0;
  font-size: 3rem;
}
.cnh-suspensa p {
  padding: 0.5rem;
  text-align: justify;
}
.cnh-suspensa h3 {
  padding: 1rem 0rem;
  text-transform: uppercase;
}
.cnh-suspensa ul {
  margin-bottom: 1rem;
}
.cnh-suspensa ul li::before {
  content: 'ðŸš«'; /*CSS Unicode*/
  font-size: 1.25rem;
  font-weight: bold;
  display: inline-block;
  width: 0.5rem;
  margin: 1rem 1.25rem 0 0rem;
}
.cnh-suspensa button {
  text-align: center;
  margin: 3rem auto;
  padding: 0.75rem;
  font-weight: 700;
  font-size: 1.5rem;
  box-shadow: 0 7px 10px #152b40;
}
/* =========== MOBILE =========== */
@media screen and (max-width: 920px) {
  .cnh-info {
    padding: 0.5rem;
  }
  .cnh-info ul li {
    display: flex;
    margin: 0.75rem 0;
  }
  .cnh-info ul li::before {
    margin: 0 1.25rem auto 0;
  }
}
